.botCard{
    background: #ffffff;
    filter: brightness(95%);
    padding: 6px;
    margin: 6px;
    display: flex;
    flex-direction: column;
    height: 200px;
    border: 1px solid #e0e0e0;
    border-radius: 5px;
    box-shadow: 0px 1px 2px rgba(0,0,0,0.2);
}
.botCard:hover{
    filter: brightness(100%);
    box-shadow: 0px 3px 5px rgba(0,0,0,0.4);
}
.botCardTitle {
    display: flex;
    flex-direction: row;
}

.botCardTitle h1 {
    font-size: 24px;
    text-align: center;
}

.botCardTitle img{
    align-self: center;
    border-radius: 50px;
    /*border: 1px solid #e0e0e0;*/
    margin: 0 14px;
}
.botCardDescription {
    flex: 1
}
.botCardStatus {
    color: #e0e0e0;
    font-style: italic;
}
.divider{
    width: 100%;
    border-top: 1px solid #e0e0e0;
    margin: 6px 0;
}

.editCard {
    background: #ffffff;
    padding: 6px;
    margin: 6px;
    display: flex;
    flex-direction: column;
    border: 1px solid #e0e0e0;
    border-radius: 5px;
    box-shadow: 0px 1px 2px rgba(0,0,0,0.2);
}
.editField {
    margin: auto;
    width: 50%;
}

input[type=text] {
    width: 100%;
    padding: 6px 10px;
    margin: 8px 0;
    box-sizing: border-box;
}
.addButton {
    margin: auto;
    width: 50%;
}